@import "./utilities";

header {
  @extend %flex-container-column;
  height: 70px;
  position: fixed;
  top: 0;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: black;
  animation: header-appear 1.2s ease;

  @keyframes header-appear {
    0% {
      height: 0px;
    }
    100% {
      height: 70px;
    }
  }
}

#navbar {
  @extend %flex-container-row;
  justify-content: space-between;
  align-items: center;
  width: 50%;

  a {
    @extend %bold-font;
    text-decoration: none;
    color: white;
    transition: 0.3s;
    animation: nav-appear 1.2s ease;

    &:hover {
      color: $orange;
    }

    .icon {
      font-size: 25px;
    }

    @keyframes nav-appear {
      0% {
        color: transparent;
      }
      100% {
        color: white;
      }
    }
  }
}
